

@function rem($size) {
  $remSize: $size / $base-value;

  @return #{$remSize}rem;
}


// $header-height: 93px;
$base-value: 16;



html {
  box-sizing: border-box;
  // scroll-padding-top: $header-height;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 1600px;
  padding: 0 70px;

  &.container{
    &--small{
      padding-left: rem(140);
    }

   &--big{
      max-width: 100%;
      padding: 0 rem(40);
    }

    &--full{
      max-width: rem(2500);
      padding: 0 rem(20);
    }
  }

  @media(max-width: 768px){
    padding: 0 20px;
    &.container{
      &--small{
        padding-left: 20px;
      }
     &--big{
        padding: 0 20px;
      }

    //   &--full{
    //     max-width: 100%;
    //     padding: 0 rem(20);
    //   }
    }
  }
}

.status{
  &-available{
    color: #27AE60;
    .product__status{
      color: #27AE60;
    }
  }
  &-not-available{
    color: #EB5757;
    .product__status{
      color: #EB5757;
    }
  }
  &-wait{
    color: #F2994A;
    .product__status{
      color: #F2994A;
    }
  }
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: Max(rem(16), 12px);
  font-family: 'Inter' , sans-serif;
  font-weight: 400;
  line-height: rem(28);
  color: var(--black);
  &.menu-open{
    overflow: hidden;
    .header{
      background-color: #fff;
    }
  }
}

.title-big >*{
  font-family: 'Inter';
  font-size: rem(50);
  line-height: rem(55);
  font-weight: 600;
  color: var(--black);

}

.title-h1 >*{
  font-family: 'Inter';
  font-size: rem(45);
  line-height: rem(60);
  font-weight: 600;
  letter-spacing: -0.02em;
  color: var(--black);
}

.title-h2 >*{
  font-family: 'Inter';
  font-size: rem(35);
  line-height: rem(45);
  font-weight: 600;
  color: var(--black);
}

.title-h3 >*{
  font-family: 'Inter';
  font-size: rem(30);
  line-height: rem(35);
  font-weight: 700;
  color: var(--black);
}

.title-h4 >*{
  font-family: 'Inter';
  font-size: rem(22);
  line-height: rem(30);
  font-weight: 700;
  color: var(--black);
}

.title-h5 >*{
  font-family: 'Inter';
  font-size: rem(20);
  line-height: rem(25);
  font-weight: 700;
  color: var(--black);
}

.title-h6 >*{
  font-family: 'Inter';
  font-size: Max(rem(15), 12px);
