}


.btn{
  display: inline-flex;
  outline: 0;
  align-items: center;
  text-align: center;
  justify-content: center;
  padding: rem(14.5) rem(27.5) rem(16);
  font-weight: 800;
  font-size: Max(rem(20), 12px);
  line-height: rem(20);
  color: var(--white);
  background-color: var(--primary);
  border-radius: rem(20);
  border: 1px solid var(--primary);
  transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
  &--medium{
    padding: rem(12) rem(20);
    font-size: rem(18);
    line-height: rem(20);
  }
  &--small{
    padding: rem(9) rem(15);
    font-size: Max(rem(16), 10px);
    border-radius: rem(14);
  }
  &--smallest {
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    padding: 2px 12px;
    border-radius: 16px;
    border: 1px solid var(--v1color, #FEB11C);
  }
  &--white{
    background-color: transparent;
    border: 2px solid var(--primary);
    color: var(--black);
    &:not(span):hover{
      background-color: var(--primary) !important;
      color: var(--white) !important;
    }
  }
  &--gray{
    background-color: var(--text-white);
    border-color: var(--text-white);
    color: var(--black);
    &:not(span):hover{
      background-color: var(--primary) !important;
      color: var(--white) !important;
    }
  }
  &.disabled,
  &.disabled:hover,
  &[disabled],
  &[disabled]:hover{
    background: var(--text-white);
    border-color: var(--text-white);
    cursor: not-allowed;
    color: #fff;
  }
  &:not(span):hover{
    color: var(--black);
    background-color: transparent;
  }

}

.socials{
  display: flex;
  li{
    display: flex;
    align-items: center;
    justify-content: center;
    &:not(:last-child){
      margin-right: rem(10);
    }
    a{
      display: inline-flex;
      align-items: center;
      justify-content: center;
      width: rem(32);
      height: rem(32);
      svg path{
        transition: fill 0.2s ease-in-out;
      }
      >*{
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
      &:hover{
        svg path{
          fill: var(--primary);
        }
      }
    }
  }

  @media(max-width: 768px){
    flex-wrap: wrap;
    margin: 0 -5px -10px !important;
    li{
      margin: 0 5px 10px !important;
    }
  }
}

.arrow-back{
  display: flex;
  align-items: center;
  cursor: pointer;
  line-height: rem(32);
  width: max-content;
  max-width: 100%;
  &__img{
    width: rem(54);
    height: rem(22);
    margin-right: rem(20);
    transition: transform 0.2s ease-in-out;
    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
      transition: transform 0.2s ease-in-out;
    }
  }
  span{
    font-weight: 500;
    line-height: rem(28);
    text-decoration-color: var(--primary) !important;
    text-underline-offset: rem(9);
    text-decoration: underline;
    transition: color 0.2s ease-in-out;
    &:not(:hover){
      color: var(--black);
    }
  }
  @media(min-width: 769px){
    &:hover{
      span{
        color: var(--primary);
      }
      img{
        transform: translateX(-19%);
      }
    }
  }

  @media(max-width: 768px){
    &__img{
      margin-right: 13px;
      width: 51px;
    }
    span{
      font-size: 14px;
      line-height: 30px;
    }
  }

}

input[type='radio']{
  position: absolute;
  width: 1px;
  height: 1px;
  opacity: 0;
}

.input{
  position: relative;
  input,
  textarea,
  &.input--file,
  .zen-ui-select__value{
    border: 1px solid var(--text-white);
    border-radius: rem(18);
    font-weight: 500;
    font-size: Max(rem(16), 12px);
    line-height: rem(27);
    width: 100%;
    height: 100%;
    outline: none;
    padding: rem(9) rem(15);
    color: var(--black);
    background: var(--white);
    &::placeholder{
      opacity: 1 !important;
      color: var(--black) !important;
    }
  }
  // input[type='number'] {
  //   -moz-appearance: textfield;
  // }
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
      -webkit-appearance: none;
  }

  input[type='file']{
    position: absolute;
    width: 1px;
    height: 1px;
    opacity: 0;
  }

  textarea {
    resize:none;
    height: rem(112);
    &:focus{
        outline: none;
    }
  }
  .select2{
    display: none;
  }
  &.input--file {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: rem(4) rem(4) rem(4) rem(15);
    label{
      flex-shrink: 0;
      margin-left: rem(19);
    }
  }
  &.input--radiocheck {
    border: none;
    input {
      position: absolute;
      left: -9999px;
      &:checked+label::after{
        opacity: 1;
      }
    }
    label {
      padding-left: rem(43);
      font-weight: 500;
      font-size: rem(16);
      line-height: rem(28);
      min-height: rem(30);
      display: flex;
      align-items: center;
      &::before{
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: rem(30);
        height: rem(30);
        border-radius: 50%;
        border: 1px solid var(--black);
      }
      &::after{
        content: '';
        position: absolute;
        left: rem(7);
        top: rem(7);
        width: rem(16);
        height: rem(16);
        border-radius: 50%;
        background: var(--primary);
        opacity: 0;
        transition: opacity .3s ease-in-out;
      }
    }

  }
  &--radio{
    position: relative;
    display: block;
    color: var(--black);
    &__btn{
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      border: 1px solid transparent;
      outline: 1px solid var(--black);
      border-radius: rem(40);
      font-weight: 600;
      font-size: rem(20);
      line-height: rem(20);
      padding: rem(7.4) rem(14.9) rem(7) rem(14);
      transition: outline-color 0.3s ease-in-out, border-color 0.3s ease-in-out;
    }
    &__check{
      position: relative;
      padding-left: rem(51);
      &::after{
          content: "";
          position: absolute;
          width: rem(30);
          height: rem(30);
          left: 0;
          top: 50%;
          transform: translateY(-50%);
          border: 1px solid var(--black);
          border-radius: rem(18);
      }
      &::before{
          content: "";
          position: absolute;
          left: rem(7);
          top: 50%;
          transform: translateY(-50%);
          width: rem(16);
          height: rem(16);
          background-color: var(--primary);
          border-radius: 50%;
          opacity: 0;
          transition: opacity 0.3s ease-in-out;
      }
    }
    input:checked + .input--radio__check::before{
      opacity: 1;
    }
    input:checked + *, .input--radio__btn:hover{
      border-color:  var(--primary);
      outline-color: var(--primary);
    }
  }
  &--checkbox {
    position: relative;
    display: block;
    cursor: pointer;
    input {
      position: absolute;
      left: -9999px;
      &:checked + span::before {
        background-color: var(--primary);
        border-color: var(--primary);
      }
    }
    > span {
      position: relative;
      padding-left: rem(35);
      display: flex;
      align-items: center;
      min-height: rem(25);
      font-size: rem(16);
      font-style: normal;
      font-weight: 400;
      line-height: rem(18);
      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: rem(25);
        height: rem(25);
        border: 1px solid var(--black);
        background-image: url('data:image/svg+xml,%3Csvg width="25" height="25" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg"%3E%3Cpath d="M6 10.9091L11.4737 16L19 9" stroke="white" stroke-width="3"/%3E%3C/svg%3E ');
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        transition: background-color .3s ease-in-out, border-color .3s ease-in-out;
      }
    }
  }


  &--select{
    position: relative;
    display: flex;
    align-items: center;

    &.is-open{
      input,
      textarea,
      .output_text{
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;
      }
      .input__arrow{
        transform: rotate(-180deg);
      }
    }
  }


  .woocommerce-input-wrapper input{
    height: rem(46);
  }
  .zen-ui-select__options {
    max-height: unset;
    padding: 0;
    border: none;
  }
  .zen-ui-select__value.disabled {
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }

  .zen-ui-select__dropdown,
  &__dropdown{
    display: none;
    position: absolute;
    z-index: -1;
    transform: translateY(100%);
    text-align: left;
    max-height: rem(200);
    overflow-y: auto;
    background: #FFFFFF;
    box-shadow: 0px 1px 23px rgba(30, 33, 36, 0.13);
    left: 0;
    bottom: 1px;
    border: 1px solid var(--text-white);
    right: 0;
    top: unset;
    &.active{
      display: block;
    }
    .zen-ui-select__option,
    li{
      cursor: pointer;
      padding: rem(7) rem(10);
      transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
      &.is-selected{
        pointer-events: none;
        color: var(--primary);
      }
      &:not(:last-child){
        border-bottom: 1px solid (var(--text-white));
      }
      &:hover{
        background-color: var(--primary);
        color: var(--white);
      }
    }

    .zen-ui-select__option--current {
      background: var(--primary) !important;
      color: var(--white) !important;
    }

  }
  .zen-ui-select__option::first-letter,
  .zen-ui-select__value-text::first-letter {
    text-transform: uppercase;
  }

  &__arrow{
    position: absolute;
    right: rem(11);
    transition: transform 0.2s ease-in-out;
    pointer-events: none;
  }
  &--required{
    position: relative;
    &::after{
      content: "*";
      position: absolute;
      right: rem(-11);
      top: 0;
      width: rem(11);
      height: rem(15);
      display: flex;
      align-items: center;
      justify-content: center;
      color: #EB5757;
    }
  }
  .woocommerce-input-wrapper.is-error,
  &.is-error{
    margin-bottom: rem(7);
    display: block;
    position: relative;
    input,
    textarea,
    .output_text,
    .zen-ui-select__value{
      border: 1px solid #EB5757;
    }
    input::placeholder{
      color: #EB5757 !important;
    }
    .input__error{
      display: block;
    }
  }
  &__error {
    position: absolute;
    left: 0;
    right: 0;
    transform: translateY(100%);
    font-size: 11px;
    bottom: -3px;
    line-height: 11px;
    color: #EB5757;
    display: none;
    z-index: -2;
  }


  @media(max-width: 768px){
    &--select{
      width: 100%;
      &:not(:last-child){
        margin-bottom: 10px;
      }
      &.is-open{
        &:first-child{
          border-bottom-right-radius: 0;
        }
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
      }
    }
  }

}

