[data-show-mini-cart]{
  position: relative;
}
.mini-cart {
  display: none;
  position: absolute;
  bottom: -10px;
  right: -5px;
  z-index: 10001;
  background: #FFF;
  box-shadow: 0px rem(8) rem(50) 0px rgba(19, 20, 21, 0.07);
  padding: rem(18) rem(25) rem(36);
  width: rem(370);
  max-width: calc(100vw - 30px);
  transform: translateY(100%);
  cursor: default;
  &.cart-empty{
    .mini-cart__btn,
    .mini-cart__total{
      display: none;
    }
  }
  &.mini-cart--wholesale{
    .mini-cart__total>*{
      display: none;
    }
  }
  &:not(.cart-empty){
    .mini-cart__list-empty{
      display: none;
    }
    .mini-cart__list-not-empty{
      display: block;
    }
  }
  &__list {
    overflow-y: auto;
    max-height: rem(200);
    padding-right: rem(20);
    margin-right: rem(-20);
    line-height: 0;
    scrollbar-color: var(--primary) transparent;
    scrollbar-width: thin;
    &::-webkit-scrollbar {
      width: 5px;
    }
    &::-webkit-scrollbar-track {
      background-color: transparent;
      border-radius: 0;
    }
    &::-webkit-scrollbar-thumb {
      background-color: var(--primary);
      border-radius: 0;
    }
    &-empty{
      font-size: max(1rem, 12px);
      font-weight: 400;
      line-height: 1.75rem;
      color: var(--black);
      text-align: left;
      display: block;
    }
    &-not-empty{
      display: none;
      text-align: left;
      font-size: rem(18);
      font-weight: normal;
      color: var(--black);
    }
  }
  &__title {
    color: var(--black);
    text-align: left;
    margin-bottom: rem(28);
  }
  &__total {
    margin-top: rem(17);
    padding-top: rem(20);
    border-top: rem(4) solid #F8F8F9;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    color: var(--black);
    font-size: rem(14);
    font-style: normal;
    font-weight: 400;
    line-height: rem(20);
  }
  &__btn {
    margin-top: rem(20);
    .btn {
      width: 100%;
    }
  }

  .cart-item {
    &:not(:last-child) {
      margin-bottom: rem(15);
    }
    &.cart-item--smallest {
      padding: 0;
      .cart-item__total {
        padding: 0;
        text-align: left;
      }
    }
    &__title {
      line-height: rem(18);
    }
  }
  .product-card__price--new {
    font-size: rem(25);
    line-height: rem(25);
  }
}

.section-title{
  display: inline-flex;
  align-items: center;
  &::after{
    content: '';
    display: block;
    width: rem(346);
    height: rem(4);
    background-color: var(--primary);
  }
  &__img{
    width: rem(54);
    height: rem(54);
    background-color: var(--primary);
    border-radius: rem(30);
    display: flex;
    align-items: center;
    justify-content: center;
    order: 1;
    >*{
      width: rem(20);
      height: rem(20);
      object-fit: contain;
    }
  }
}

.burger{
  width: 20px;
  height: 18px;
  position: relative;
  margin-right: 10px;
  >*{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
    transition: opacity 0.3s ease-in-out;
  }
  >*:last-child{
    opacity: 0;
  }
  &.is-active{
    >*:first-child{
      opacity: 0;
    }
    >*:last-child{
      opacity: 1;
    }
  }
}

.breadcrumbs{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  li{
    font-weight: 400;
    font-size: rem(45);
    line-height: rem(55);
    color: var(--black);
    &:not(:last-child){
      margin-right: rem(6);
      padding-right: rem(35);
      position: relative;
      &::after{
        content: '';
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        width: rem(16);
        height: rem(16);
        background-image: url(./../img/ico-breadcrumbs.svg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
    }
    a{
      color: var(--black);
      font-weight: 600;
      line-height: rem(60);
      letter-spacing: -0.02em;
      transition: color 0.3s ease-in-out;
      &:hover{
        color: var(--primary);
      }
    }
  }
  @media(max-width: 768px){
    li{
        font-size: 32px;
        line-height: 45px;
    }
  }
}

.tag{
  font-weight: 700;
  font-size: Max(rem(18), 10px);
  line-height: rem(22);
  text-transform: uppercase;
  color: var(--white);
  padding: rem(4) rem(12) rem(4) rem(18);
  background-color: var(--red);
  border-top-right-radius: rem(16);
  pointer-events: none;
  &:not(:last-child){
    margin-bottom: rem(5);
  }
  &--new{
    background-color: #29AF08;
  }
}

.quantity{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__btn{
    width: rem(19);
    height: rem(40);
    display: flex;
    align-items: center;
    >*{
      width: rem(14);
      height: rem(14);
      object-fit: contain;
    }
    &:last-child{
      justify-content: flex-end;
    }
  }
  &__input{
    min-height: rem(40);
    width: rem(35);
    font-weight: 600;
