.dropdown{
  position: relative;
  padding: rem(7) rem(15) rem(8);
  margin-right: 0 !important;
  transition: background-color .3s ease-in-out;
  cursor: pointer;
  @media (min-width: 769px) {
      &:not(.dropdown-click):hover {
        background-color: rgba(42, 46, 50, 0.05);
        ul {
          opacity: 1;
          visibility: visible;
        }
      }
    }
  ul {
    position: absolute;
    z-index: 99;
    bottom: 0;
    right: 0;
    transform: translateY(100%);
    text-align: right;
    width: max-content;
    background: #FFFFFF;
    box-shadow: 0px 1px 23px rgba(30, 33, 36, 0.13);
    opacity: 0;
    visibility: hidden;
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
    &.active{
      z-index: 1000;
      opacity: 1 !important;
      visibility: visible !important;
    }
    a{
      display: block;
      padding: rem(7) rem(16) rem(8);
      font-weight: 400;
      font-size: Max(rem(14), 10px);
      line-height: rem(20);
      color: var(--black);
      transition: background-color .3s ease-in-out;
      @media (min-width: 769px) {
        &:hover {
            background-color: rgba(42, 46, 50, 0.05);
        }
      }
      &.is-active{
        background-color: var(--primary);
        color: #fff;
      }
    }

    .separated {
      margin-top: 10px;
      padding-top: 2px;
      position: relative;
      &::before {
        content: '';
        position: absolute;
        top: 0;
        right: 1rem;
        height: 2px;
        width: 106px;
        max-width: 100%;
        background: #F4F4F4;
    }
    }
  }
}

