.popup{
  width: rem(495);
  max-width: 100%;
  background: #FFFFFF;
  box-shadow: 0 8px 50px rgba(19, 20, 21, 0.15);
  padding: rem(56) rem(30) rem(62);
  .form {
    margin: rem(16) auto 0;
    max-width: rem(300);
    &__row:not(:last-child) {
      margin-bottom: 12px;
    }
    &__input {
      width: calc(100% + 0.625rem);
    }
  }
  .f-button {
    top: rem(15)!important;
    right: rem(15)!important;
    width: rem(22);
    height: rem(22);
    border: none !important;
    outline: none !important;
    background: transparent!important;
    --f-button-bg: transparent!important;
    svg {
      width: rem(22);
      height: rem(22);
      stroke: #828282;
    }
  }
  &__title {
    margin-bottom: rem(30);
  }

  &.popup--centered{
    text-align: center;
    .input {
      input {
        text-align: center;
      }
      &__error{
        text-align: center;
      }
    }
  }
  &:not(.popup--centered){
    width: rem(640);

    .form {
      max-width: 100%;
      margin-left: 0;
      &__input:not(.form__textarea) {
        max-width: 310px;
      }
    }
    .popup__inner{
      max-width: 420px;
      margin: 0 auto;
      width: 100%;
    }

  }

  @media (max-width: 768px) {
    .popup {
      padding: 56px 20px 28px;
      &__title {
        margin-bottom: 20px;
      }
      &__inner .success {
        margin-top: -20px;
        margin-bottom: -20px;
      }
    }
  }
}

.dropdown-click{
  display: inline-flex;
  align-items: center;
  padding: 0;
  img{
    flex-shrink: 0;
    &:first-child{
      width: rem(15);
      height: rem(10);
      object-fit: contain;
      margin-right: rem(7);
    }
    &:last-child{
      width: rem(12);
      height: rem(12);
      object-fit: contain;
      margin-left: rem(7);
    }
  }
  ul{
    bottom: rem(-4);
  }
  span{
    font-weight: 400;
    font-size: Max(rem(14), 12px);
    line-height: rem(20);
  }

}

.pagination{
  display: flex;
  align-items: center;
  >*{
    display: block;
  }
  .page-numbers{
    font-weight: 600;
    font-size: Max(rem(16), 10px);
    line-height: rem(25);
    color: var(--black);
    transition: color 0.3s ease-in-out;
    &:hover{
      color: var(--primary);
    }
    &:not(:last-child){
      margin-right: rem(39);
    }
    &:last-child{
      position: relative;
      margin-left: rem(186);
      &::before{
        content: '';
        position: absolute;
        left:  rem(-186);
        top: 50%;
        transform: translateY(-50%);
        width: rem(146);
        height: rem(4);
        background-color: var(--gray);
        pointer-events: none;
      }
    }
    &.current{
      padding: rem(4.3) rem(12.2);
      background-color: var(--primary);
      color: var(--white);
      margin-right: rem(27);
    }
    &.next, .prev{
      display: none;
    }
  }
  @media(max-width: 768px){
    .page-numbers{
      &:not(:last-child){
        margin-right: 30px;
      }
      &:last-child{
        flex-grow: 1;
        margin-left: 0;
        text-align: right;
        display: flex;
        align-items: center;
        &::before{
          position: static;
          transform: none;
          width: auto;
          flex-grow: 1;
          margin-right: 16px;
        }
      }
      &.current{
        margin-right: 18px;
      }
    }
  }
}


