.swiper-nav-pagination {
  position: absolute;
  left: 0;
  bottom: rem(66);
  display: flex;
  align-items: center;
  max-width: rem(450);
  width: 100%;
  justify-content: space-between;
}
.swiper-button-next, .swiper-button-prev {
  width: rem(25);
  height: rem(25);
  margin-top: 0;
  line-height: 0;
  position: static;
  flex-shrink: 0;
  outline: none!important;
  border: none!important;
}
.swiper-pagination{
  position: relative;
  margin: 0 rem(20);
  bottom: 0 !important;
  display: flex;
  width: 100%;
  height: rem(4)!important;
  &-bullet {
    margin: 0 rem(5);
    position: relative;
    width: 100%;
    height: 100%;
    display: block;
    border-radius: rem(14);
    background: var(--text-white);
    opacity: 1;
    &::before {
      position: absolute;
      content: "";
      left: 0;
      top: 0;
      height: 100%;
      background-color: var(--primary);
      width: 0;
      z-index: 2;
      border-radius: inherit;
    }
  }
  &.can-play{
    .swiper-pagination-bullet-active {
      transition-duration: 3000ms;
      pointer-events: none;
      &::before {
        transition: width 3000ms linear!important;
        transition-duration: inherit!important;
        width: 100%;
      }
    }
  }
}
.swiper-button-prev::after, .swiper-rtl .swiper-button-next::after,
.swiper-button-next::after, .swiper-rtl .swiper-button-prev::after {
  content: none;
}
.swiper-nav {
  path{
    transition: stroke .3s ease-in-out;
  }
  &:hover{
    path{
      stroke: var(--primary);
    }
  }
  & > * {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

.image-parallax{
  overflow: hidden;
}

